import React from 'react'
import { connect } from 'react-redux'
import ActionButtons from '../../../components/ActionButtons'
import Searchbar from '../../../components/Searchbar'
import Table from '../../../components/Table'
import TitleViews from '../../../components/TitleViews'
import { activeViewDataCustomer, applyDataToFormCustomer, getCustomers, openFormCustomerData, selectedDataCustomer, updateCustomerForm } from '../../../redux/actions/CustomerActions'
import CreateCustomer from '../create_customer'
import ViewCustomerData from '../view_customer'

const mapPropsToState = (state) => {
    return{
        customerList: state.Customer.list,
        isOpenViewDataCustomer: state.Customer.viewCustomer.isOpenViewDataCustomer,
        customerSelected: state.Customer.viewCustomer.customerSelected,
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        getCustomers : () => dispatch(getCustomers()),
        applyDataToFormCustomer : (value) => dispatch(applyDataToFormCustomer(value)),
        openFormCustomerData : (value) => dispatch(openFormCustomerData(value)),
        updateCustomerForm : (value) => dispatch(updateCustomerForm(value)),
        activeViewDataCustomer : (value) => dispatch(activeViewDataCustomer(value)),
        selectedDataCustomer : (value) => dispatch(selectedDataCustomer(value)),
    }
}

const ListCustomer = connect(mapPropsToState, mapDispatchToProps)(({ customerList, isOpenViewDataCustomer, customerSelected, ...actions }) => {
    const { getCustomers, applyDataToFormCustomer, openFormCustomerData, updateCustomerForm, activeViewDataCustomer, selectedDataCustomer } = actions
    const actionsTable = true ?  {
        field: ' ',
        headerName: ' ',
        renderCell : (params) => {
            return <ActionButtons
                disabled={false}
                onEdit={() =>{
                        applyDataToFormCustomer(params.row)
                        updateCustomerForm(true)
                        openFormCustomerData(true)
                    }
                }
                onViewData={() => {
                    selectedDataCustomer(params.row)
                    activeViewDataCustomer(true)
                }}
            />
        },
        flex: 1,
    } : null
    
    const columns = [
        {
            field: 'fullname',
            headerName: 'Cliente',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params)
                return(
                    <>{params.row.firstName} {params.row.lastName}</>
                )
            },
        },{
            field: 'documentNumber',
            headerName: 'Numero de documento',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params)
                return(
                    <>{params.row.documentNumber}</>
                )
            },
        },{
            field: 'phoneNumber',
            headerName: 'Numero de telefono',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params)
                return(
                    <>{params.row.phoneNumber}</>
                )
            },
        },{
            field: 'localNumber',
            headerName: 'Numero de telefono local',
            editable: false,
            flex : 1,
            renderCell : (params) => {
                // console.log(params)
                return(
                    <>{params.row.localNumber}</>
                )
            },
        },{...actionsTable}
    ]

    React.useEffect(() => {
        getCustomers()
    },[])
    return(
        <>
            <TitleViews
                title="Lista de clientes"
                mb={2}
            />
            <Searchbar />
            <Table
                columns={columns}
                rows={customerList}
                loading={true}
            />

            <CreateCustomer />
            <ViewCustomerData
                isOpen={isOpenViewDataCustomer}
                close={activeViewDataCustomer}
                customerSelected={customerSelected}
                selectedDataCustomer={selectedDataCustomer}
            />
        </>
    )
})

export default ListCustomer