import React from 'react'
import Dialog from '../../../components/Dialog'
import { ShowData } from '../../products/view_product_data'

const ViewCustomerData = ({isOpen, customerSelected={}, ...actions}) => {
    const { close, selectedDataCustomer } = actions
    React.useEffect(() => {
        if(!isOpen){
            selectedDataCustomer({})
        }
    },[isOpen])
    return(
        <>
            <Dialog open={isOpen} close={() => close(false)} title="Datos del cliente" maxWidth='xs' >
                <ShowData title="Nombre completo" text={`${customerSelected.firstName??""} ${customerSelected.lastName??""}`} />
                <ShowData title="Correo electronico" text={`${customerSelected.email??""}`} />
                <ShowData title="Numero de telefono" text={`${customerSelected.phoneNumber??""}`} />
                <ShowData title="Numero de telefono local" text={`${customerSelected.localNumber??""}`} />
                <ShowData title="Direccion de domicilio" text={`${customerSelected.address??""}`} />
                <ShowData title="Nombre de la tienda" text={`${customerSelected.storeName??""}`} />
                <ShowData title="Direccion de la tienda" text={`${customerSelected.storeAddress??""}`} />
                <ShowData title="Numero de documento" text={`${customerSelected.documentNumber??""}`} />
            </Dialog>
        </>
    )
}

export default ViewCustomerData