import constant from "."
import Customer from "../../views/customers/class"

export const saveCustomerList = (list) => {
    return {type: constant.SAVE_CUSTOMER_LIST, list}
}
export const openFormCustomerData = (value) => {
    return {type: constant.OPEN_FORM_CUSTOMER_DATA, value}
}
export const updateDataCustomerForm = (value) => {
    return {type: constant.UPDATE_DATA_CUSTOMER_FORM, value}
}
export const applyDataToFormCustomer = (customer) => {
    return {type: constant.APPLY_DATA_TO_FORM_CUSTOMER, ...customer}
}
export const restoreDataForm = () => {
    return {type: constant.RESTORE_DATA_FORM}
}
export const updateCustomerForm = (value) => {
    return {type: constant.UPDATE_CUSTOMER_FORM, value}
}
export const activeViewDataCustomer = (value) => {
    return {type: constant.ACTIVE_VIEW_DATA_CUSTOMER, value}
}
export const selectedDataCustomer = (customer) => {
    return {type: constant.SELECTED_DATA_CUSTOMER, customer}
}
export const getCustomers = () => async (dispatch) => {
    try{
        const customers = new Customer({})
        const list = await customers.get()
        if( list.status ){
            dispatch(saveCustomerList(list.payload))
        }
    }catch(err){
        console.log(err)
    }
}