import React from 'react'
import { Order } from '../../../components/icons'
import Searchbar from '../../../components/Searchbar'
import Table from '../../../components/Table'
import TitleViews from '../../../components/TitleViews'

const OrderList = () => {
    const columns = [
        {
            field: 'name',
            headerName: 'Pedido',
            editable: false,
            flex : 1,
        },{
            field: 'customer',
            headerName: 'Cliente',
            editable: false,
            flex : 1,
        },{
            field: 'sale',
            headerName: 'Venta',
            editable: false,
            flex : 1,
        },{
            field: 'status',
            headerName: 'Estado',
            editable: false,
            flex : 1,
        },{
            field: ' ',
            headerName: ' ',
            editable: false,
            flex : 1,
        }
    ]
    return(
        <>
            <TitleViews
                title="Lista de pedidos"
                icon={<Order />}
                mb={3}
            />
            <Searchbar />
            <Table
                rows={[{
                    _id: 1,
                    name: "PEDIDO #2563254",
                    customer: "Enrique Pacheco",
                    status: "Entregado",
                    sale: "$ 1.200.658",
                },{
                    _id: 2,
                    name: "PEDIDO #2563251",
                    customer: "Jose Pacheco",
                    status: "En curso",
                    sale: "$ 2.589.326",
                }]}
                columns={columns}
                rowCount={10}
                page={1}
                loading={true}
            />
        </>
    )
}

export default OrderList