import constant from '../actions/index'

const createCustomerForm = {
    firstName: "",
    lastName: "",
    email: "",
    phoneNumber: "",
    localNumber: "",
    address: "",
    storeAddress: "",
    documentNumber: "",
    storeName: ""
}

const initialState = {
    list: [],
    createCustomer: {
        openForm: false,
        form: {...createCustomerForm}
    },
    updateCustomer:{
        isUpdateForm: false
    },
    viewCustomer: {
        isOpenViewDataCustomer: false,
        customerSelected: {}
    }
}

export default function CustomerReducer(state = initialState, action){
    const globalState = {...state}
    switch(action.type){
        case constant.ACTIVE_VIEW_DATA_CUSTOMER:
            globalState.viewCustomer.isOpenViewDataCustomer = action.value
        break
        case constant.SELECTED_DATA_CUSTOMER:
            globalState.viewCustomer.customerSelected = action.customer
        break
        case constant.SAVE_CUSTOMER_LIST:
            globalState.list = action.list
        break
        case constant.UPDATE_DATA_CUSTOMER_FORM:
            globalState.createCustomer.form = action.value
        break
        case constant.OPEN_FORM_CUSTOMER_DATA:
            globalState.createCustomer.openForm = action.value
        break
        case constant.RESTORE_DATA_FORM:
            globalState.createCustomer.form = {...createCustomerForm}
            globalState.updateCustomer.isUpdateForm = false
        break
        case constant.UPDATE_CUSTOMER_FORM:
            globalState.updateCustomer.isUpdateForm = action.value
        break
        case constant.APPLY_DATA_TO_FORM_CUSTOMER:
            globalState.createCustomer.form.firstName = action.firstName
            globalState.createCustomer.form.lastName = action.lastName
            globalState.createCustomer.form.email = action.email
            globalState.createCustomer.form.phoneNumber = action.phoneNumber
            globalState.createCustomer.form.localNumber = action.localNumber
            globalState.createCustomer.form.address = action.address
            globalState.createCustomer.form.storeAddress = action.storeAddress
            globalState.createCustomer.form.documentNumber = action.documentNumber
            globalState.createCustomer.form.storeName = action.storeName
        break
        default :
            return globalState
    }
     return globalState
}