import { Box, Divider, Grid, IconButton, Paper, Typography } from '@material-ui/core';
import React from 'react';
import { Checkbox } from '../../../components/Checkbox';
import HelpDescription from '../../../components/HelpDescription';
import TextField from '../../../components/TextField';
import TextFieldMask from '../../../components/TextFieldMask';
import AddRoundedIcon from '@material-ui/icons/AddRounded';
import CloseRoundedIcon from '@material-ui/icons/CloseRounded';
import Tooltip, { CustomizeTooltip } from '../../../components/Tooltips';
import Product from '../class';
import Market from '../../market/class'
import Switch from '../../../components/Switch';

const ActionButton = ({ type="add", onClick, title, color="primary" }) => {
    return(
        <Box display="flex" width="100%" justifyContent="end" >
            <Tooltip title={title} placement='left' >
                <IconButton color={color} size='small' onClick={onClick} >
                    {type === "add" ? <AddRoundedIcon /> : <CloseRoundedIcon />}
                </IconButton>
            </Tooltip>
        </Box>
    )
}

const PriceForm = ({ priceList=[], updateProductPriceList, updateSystemMessage, calculatePrice, callNotificationSnackbar, compount }) => {
    const market = new Market()
    const add = () => {
        const product = new Product()
        const result = product.addNewPrice()
        if( result ){
            updateProductPriceList(result)
        }else{
            updateSystemMessage('LIMIT_PRICES_ALER','',5, true)
        }
    }
    const remove = (index) => {
        const product = new Product()
        const result = product.removeOnePrice(index)
        updateProductPriceList(result)
    }
    const update = (index, payload) => {
        const { attr, value } = payload
        const product = new Product()
        const result = product.updatePriceList(index, attr, value)
        if( result === "error favorite price" ){
            callNotificationSnackbar(true, "Solo puede tener 1 (uno) precio principal en la lista", "warning")
            return
        }
        updateProductPriceList(result)
    }

    return(
        <>
            <Box mb={2} >
                <Paper variant='outlined' elevation={0} >
                    <Box display="flex" flexDirection="column" p={2} maxHeight="500px" overflow="hidden auto" >
                        { priceList.map((price, index) => {
                            return(
                                <React.Fragment key={index}  >
                                    <Box display="inline-flex" >
                                        <CustomizeTooltip 
                                            title={<p> <b>Asignar como precio de venta.</b> <br /> Este sera el precio visible para los clientes en la tienda virtual, en caso de no seleccionar ninguno sera asignado el primer precio de la lista como precio de venta para los clientes </p>} >
                                                <Box>
                                                    <Switch
                                                        name='favoriteSalePrice'
                                                        checked={price.favoriteSalePrice}
                                                        onChange={(value,name) => {
                                                            update(index, {attr: name, value})
                                                        }}

                                                    />
                                                </Box>
                                        </CustomizeTooltip>
                                    </Box>

                                    <Box>
                                        { index <= 0 ? <ActionButton title="Añadir nuevo precio" onClick={add} type="add" /> : <ActionButton title="Remover precio" type="remove" onClick={() => remove(index)} color="secondary" /> }
                                        <TextField
                                            required
                                            label="Identificador"
                                            variant="outlined"
                                            value={price.identifier}
                                            name="identifier"
                                            onChange={(elem) => update(index, elem)}
                                        />
                                        {index >= 1 ? null : 
                                            <TextField
                                                
                                                required
                                                label="Costo del producto"
                                                variant="outlined"
                                                value={price.cost}
                                                name="cost"
                                                // disabled={index >= 1 ? true : false}
                                                disabled={compount}
                                                onChange={(elem) => update(index, elem)}
                                                InputProps={{
                                                    inputComponent : TextFieldMask
                                                }}
                                                inputProps={{
                                                    style : { fontWeight : "600" },
                                                    value : price.cost
                                                }}
                                            />
                                        }
                                        <TextField
                                            label="Precio de venta"
                                            variant="outlined"
                                            value={price.salePrice}
                                            name="salePrice"
                                            disabled={calculatePrice}
                                            onChange={(elem) => update(index, elem)}
                                            InputProps={{
                                                inputComponent : TextFieldMask
                                            }}
                                            inputProps={{
                                                style : { fontWeight : "600" },
                                                value : price.salePrice
                                            }}
                                        />
                                        <TextField
                                            label="Porcentaje de ganancia"
                                            variant="outlined"
                                            value={price.percentageProfit}
                                            disabled={!calculatePrice}
                                            name="percentageProfit"
                                            onChange={(elem) => update(index, elem)}
                                            InputProps={{
                                                inputComponent : TextFieldMask,
                                                endAdornment : calculatePrice ? <HelpDescription title="El precio de venta sera calculado siempre en base al porcentaje registrado en este campo" /> : false
                                            }}
                                            inputProps={{
                                                prefix : "%",
                                                format : "###%",
                                                value : price.percentageProfit
                                            }}
                                        />
                                        <Typography component="span" >
                                            <Box>
                                                Ganancia : <Box component="span" fontWeight="bold" >$ { market.currencyFormat(price.profit) } </Box>
                                            </Box>
                                        </Typography>
                                    </Box>
                                    <Box my={2} >
                                        <Divider />
                                    </Box>
                                </React.Fragment>
                            )
                        }) }
                        {/* <Checkbox
                            checked={calculatePrice}
                            name="calculatePrice"
                            label="Calcular precio de venta por el porcentaje"
                            onChange={ (res) => calculatePricePerPercentage(res.value) }
                        /> */}
                    </Box>
                </Paper>
            </Box>
        </>
    )
}

export default PriceForm