import app from "../../connection/Api"
import history from "../../handlers/history"
import parseData from '../../methods/parseData'

export default class Customer{
    constructor({ page=1, limit=10, filter={}, data={}, methods={} }){
        this.data = data
        this.page = page
        this.limit = limit
        this.filter = filter
        this.methods = methods
    }
    updateFormAtributes = ({attr, value}) => {
        const form = parseData(this.data)
        form[attr] = value
        this.methods.updateForm(form)
    }
    get = async () => {
        try {
            let url = `/find/customer?page=${this.page}&limit=${this.limit}&`
            for(let key of Object.keys(this.filter) ){
                if( this.filter[key] === undefined ||  this.filter[key] === null ){
                    delete this.filter[key]
                }else{
                    url = url+`${key}=${this.filter[key]}&`
                }
            }
            const customersList = await app.get(url)
            return customersList
        } catch (error) {
            console.log(error)
        }
    }
}

export const goManageBusiness = (businessName) => history.push(`/${businessName}/manage/customers`)