import { Box, Grid, Paper } from '@material-ui/core'
import React from 'react'
import Dialog from '../../../components/Dialog'
import TextField from '../../../components/TextField'
import Button from '../../../components/Button'
import Customer from '../class'

const Form = ({openForm, form, isUpdateForm, ...actions}) => {
    const { openFormCustomerData, updateDataCustomerForm, restoreDataForm } = actions
    const customer = new Customer({
        data: form,
        methods: {
            updateForm: updateDataCustomerForm,
        }
    })

    React.useEffect(() => {
        if( !openForm ){
            restoreDataForm()
        }
    },[openForm])
    return(
        <>
            <Dialog open={openForm} close={() => openFormCustomerData(false)} title={isUpdateForm ? 'actualizar datos del cliente' : 'crear cliente'} >
                <Paper variant='outlined'>
                    <Box p={3} >
                        <Grid container spacing={1} >
                            <Grid item xs={6} >
                                <TextField
                                    label="Nombres"
                                    name="firstName"
                                    onChange={customer.updateFormAtributes}
                                    value={form.firstName}
                                    required
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <TextField
                                    name="lastName"
                                    label="Apellidos"
                                    onChange={customer.updateFormAtributes}
                                    value={form.lastName}
                                    required
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <TextField
                                    name="documentNumber"
                                    label="Numero de documento"
                                    value={form.documentNumber}
                                    required
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <TextField
                                    name="storeName"
                                    label="Nombre de la tienda fisica"
                                    value={form.storeName}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    name="email"
                                    label="Correo electronico"
                                    value={form.email}
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <TextField
                                    name="phoneNumber"
                                    label="Numero de telefono"
                                    value={form.phoneNumber}
                                />
                            </Grid>
                            <Grid item xs={6}>
                                <TextField
                                    name="localNumber"
                                    label="Numero de telefono local"
                                    value={form.localNumber}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    name="address"
                                    label="Direccion del domicilio"
                                    value={form.address}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    name="storeAddress"
                                    label="Direccion de la tienda fisica"
                                    value={form.storeAddress}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <Button size="large" >{isUpdateForm ? 'Actualizar datos' : 'crear cliente'}</Button>
                            </Grid>
                        </Grid>
                    </Box>
                </Paper>
            </Dialog>
        </>
    )
}

export default Form